import Vue from 'vue'
import App from './App'
import router from './router'

// 引入 Element-ui组件
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
Vue.use(ElementUI)

// 引入 Vuex
import store from './store'

// 引入转换时间插件
import Util from './helpers/utli'
Vue.use(Util)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



